@import url(https://fonts.googleapis.com/css?family=Open+Sans);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@600;700&display=swap);
@media (min-width: 1050px) {
  #title {
    font-size: 3em !important;
  }

  h1 {
    font-size: 2em;
  }

  h2 {
    font-size: 1.5em !important;
    line-height: 1.6em !important;
  }

  h3 {
    font-size: 1.2em !important;
  } 

  p {
    font-size: 1em !important;
    line-height: 1.6em !important;
  }
}

body {
  margin: 0;
  font-family: 'Open Sans',serif, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2C2C2F; 
} 

.content {
  animation: fadeInAnimation ease-in .7s; 
  animation-iteration-count: 1; 
  animation-fill-mode: forwards;
}

@keyframes fadeInAnimation { 
  0% { 
      opacity: 0; 
  } 
  100% { 
      opacity: 1; 
   } 
} 

/* navbar */
.nav-logo {
  width: 10em;
}

.footer-logo {
  width: 15em;
}

.custom-nav {
  font-size: 0.875em;
  background-color: white;
  padding: 1em;
}

.nav-links {
  width: 100%;
  display: flex;
  justify-content: flex-end;
}

.nav-tab {
  border-bottom: solid 4px #E7E7E7;
  color: #2C2C2F !important;
  text-decoration: none;
  padding-left: 2em !important;
  padding-right: 2em !important;
  font-weight: 600;
}

.nav-active {
  border-bottom: solid 4px #F07545;
}

.nav-dropdown { 
  padding-left: 2em;
  padding-right: 2em;
  margin-bottom: 0;
}

#basic-nav-dropdown {
  color: #2C2C2F !important;
}

.nav-btn {
  margin: auto 2em;
}

.dropdown-item:active {
  background-color: #F07545 !important;
}

/* landing page */
#landing {
  background: url(/static/media/landing.bf315a69.png) no-repeat center center;
  background-size: cover;
  height: 60vh;
  margin-top: 0;
  width: auto;
  text-align: right;
  padding: 10% 10vw;
}

#welcome-txt {
  text-align: left;
  font-size: 1.5em;
  margin: 0;
}

.landing-boxes-small {
  height: 6vw;
}

.landing-boxes {
  height: 7vw;
}

#identity-div {
  text-align: right;
}

#identity-txt {
  font-weight: 600;
  width: 100%;
}

.landing-content-R {
  display:inline-block;
  text-align: right;
  padding-left: 0;
  left:0;
  color: white;
}

.landing-btn {
  background: none;
  border: none;
  color: white;
  font-size: 1.2em;
  font-weight: 600;
  text-decoration: underline;
}

.landing-btn:hover {
  color: white;
  font-weight: 700;
}

#title {
  font-weight: 600;
  margin: 0;
}

/* service landing section */
#service-landing {
  background-color: #F8F8F9;
  height: 24vh;
}

.service-box {
  background-color: white;
  margin: -10vh 1em;
  width: 25vw;
  text-align: center;
  padding: 2em 1em 1em 1em;
  box-shadow: 5px 5px 10px 0px rgba(0, 0, 0, 0.1);
}

.service-box p {
  color: #727574;
}

/* how it works section */

#how-it-works-landing {
  background-color: #F1F2F4;
  padding: 5%;
}

#how-it-works-title {
  font-weight: 700;
  margin-bottom: 1em;
}

#how-it-works-LC {
  width: 45%;
}

#how-it-works-RC {
  width: 45%;
}

#how-it-works-RC h3{
  font-weight: 600;
}

#how-form {
  margin: 0 auto;
  font-weight: 600;
  font-size: 0.8em;
  width: 400px;
  background-color: white;
  padding: 4em 2em;
  border-radius: .2em;
  box-shadow: 5px 5px 10px 0px rgba(0, 0, 0, 0.1);
}

input {
  border-radius: .2em !important;
}

.form-col {
  width: 48%;
}

.form-col:nth-child(1) {
  margin-right: 4%;
} 

#how-form button {
  width: 100%;
  margin-top: 1em;
}

.how-step {
  padding-right: .5em;
}

.how-step-desc {
  margin-top: .5em;
  width: 85%;
}

.how-step-desc p {
  margin-top: 1em;
}

.form-btn {
  margin-top: 2em;
}

.instruction {
  margin-top: 1em;
}

.instruction h1 {
  color: #1DBC98;
  font-weight: 600;
}

.instruction h2 {
  font-weight: 600;
}


/* who we are */
#who-we-are {
  padding: 5%;
  background-color: #F8F8F9;
}

#who-we-are-L a {
  display: block;
  margin-top: 2em;
}

#who-we-are-L {
  padding-top: 2em;
  padding-left: 4em;
}

#who-we-are-L h2 {
  font-weight: 700;
}

#who-we-are-L p {
  margin-top: 2em;
  padding-left: 2em;
  border-left: solid 4px #F07545;
  width: 40vw;
  margin-bottom: 2em;
}

#who-we-are-R {
  text-align: center;
}

/* footer */
#footer {
  background-color: #F1F2F4;
  margin: 0;
  padding: 2em 4em 1em 4em;
}

#footer .col {
  margin-bottom: 2em;
}

#address-footer {
  margin-top: 2em;
  text-align: center;
  border-top: solid 1px #BFBFBF;
  padding-top: 2em;
  
}

#footer h3 {
  font-weight: 700;
  margin-bottom: 1em;
}

#footer a {
  color: #2C2C2F;
}

.footer-link {
  margin-bottom: .5em;
}

.contact-item {
  margin-left: .5em;
}

.contact-img {
  margin-top: .2em;
  height: 1.5em;
}

.contacts {
  width: 30%;
}
/* about page */

.about-header {
  background: url(/static/media/about-banner.146f2b55.png) no-repeat center center;
}

.about-content {
  padding: 5%;
  background-color: #F8F8F9;
  padding-bottom: 10em;
}

.about-content h2 {
  font-weight: 600;
  margin-bottom: 1em;
}

.about-text-L {
  width: 60%;
  padding-right: 4em;
}

.about-text-R {
  width: 60%;
  padding-left: 4em;
}

.about-img {
  width: 40%;
}

.about-img img {
  max-width: 100%;
}

.about-content-sub {
  margin-bottom: 6em;
}

/* philosophy section */
#phil-section {
  display: flex;
  background-color: #F1F2F4;
  padding-bottom: 2em;
}

#phil-box {
  margin: -15vh 10% 0 10%;
  padding: 2em;
  width: 100%;
  background-color: #FFFFFF;
  box-shadow: 5px 5px 10px 0px rgba(0, 0, 0, 0.1);
}

#phil-title {
  font-weight: 600;
  border-bottom: solid 1px #E7E7E7;
  padding-bottom: .5em;
  width: 100%;
}

#phil-box .col {
  padding: 2em;
}

#phil-box .col:nth-child(1), #phil-box .col:nth-child(2) {
  border-right: solid 1px #E7E7E7;
}


#phil-box h3 {
  margin-top: 2em;
  font-weight: 600;
}

/* commitments section */

#commitments {
  padding: 5%;
  background-color: #F8F8F9;
}

#commitments h1{
  font-weight: 600;
  margin-bottom: 1em;
}

.value {
  color: #1DBC98;
  font-weight: 700;
  margin: 1em 2em;
  padding: 1em 5em;
  background-color: #FFFFFF;
  box-shadow: 5px 5px 10px 0px rgba(0, 0, 0, 0.1);
  width: 25vw;
}

/* get quote box */
#service-page-nav {
  margin-top: 2em;
}

.service-page-box {
  background-color: white;
  box-shadow: 5px 5px 10px 0px rgba(0, 0, 0, 0.1);
  width: 320px;
  min-width: 70%;
  margin: 0 auto;
}

#service-page-nav a { 
  color: #2C2C2F;
}

#service-page-nav a:hover { 
  color: #1DBC98;
  font-weight: 700;
  text-decoration: none;
}

.service-page-box h3 {
  text-align: center;
  font-weight: 600;
  background-color: #1DBC98;
  padding: 1em;
  color: #FFFFFF;
}

.service-page-content {
  padding: .5em 1em 1em 1em;
}

.service-page-btn {
  display: block;
  margin-top: 1em;
}

.service-page-nav-item {
  padding: 1em;
  margin: 0 1em;
}

li p {
  margin: .5em !important;
}

.service-page-nav-item:nth-child(1), .service-page-nav-item:nth-child(2) {
  border-bottom: solid 1px #E7E7E7;
}

/* shipping service page */
.service-page {
  background-color: #F8F8F9;
  padding: 5%;
}

.service-R {
  width: 70%;
  padding: 0 4em;
}

.service-R .row { 
  margin-top: 2em;
}

.service-R .col:nth-child(1) { 
  margin-right: 4em;
}

.service-L {
  width: 30%;
}

.service-R h3 {
  font-weight: 600;
  margin-bottom: 1em;
}


.shipping-header {
  background: url(/static/media/shipping-banner.2ac7d9d5.png) no-repeat center center;
}

.header-img {
  height: 30vh;
  padding-top: 10vh;
  background-size: cover;
  justify-content: center;
}

.header {
  color: #FFFFFF;
  font-weight: 700;
}

/* form submit */

#form-submit-pg {
  background-color: #F8F8F9;
  text-align: center;
  width: 100%;
  margin: 0;
  padding: 4em;
  height: 50vh;
}

.theme-btn {
  text-align: center;
  font-size: 1em;
  outline: none;
  background-size: 100% 200%;
  background-image: linear-gradient(to top, #F07545 50%, #1DBC98 50%);
  transition: background-position .2s;  
  padding: .8em 1em;
  width: 10em;
  border: none;
  color: white;
  font-weight: 600;
  border-radius: .2em;
}

.theme-btn:hover {
  color: white;
  font-weight: 700;
  text-decoration: none;
  background-position: 0 100%;
}
/* ipad */
@media (min-width: 767px) and (max-width: 1050px) {

    #title {
      font-size: 2em !important;
    }
  
    h1 {
      font-size: 1.5em !important;
    }
  
    h2 {
      font-size: 1em !important;
    }
  
    h3 {
      font-size: .9em !important;
    }
  
    p {
      font-size: .7em !important;
    }
  
    label {
      font-size: .8em;
    }

    .custom-nav {
      font-size: 0.6em !important;
      background-color: white;
      padding: .5em !important;
    }

    .dropdown-item {
      font-size: .7em !important;
    }
  
    .footer-logo {
      width: 10em !important;
    }
  
    #landing {
      max-height: 350px !important;
      padding-top: 10%;
    }
  
    #service-landing {
      max-height: 150px !important; 
    }
  
    .service-box {
      margin: -3em .5em 0 .5em;
      padding: 1.5em .5em .5em .5em;
    }
  
    .landing-boxes-small {
        height: 5vw !important;
    }
    
    .landing-boxes {
        height: 6vw !important;
    }

    /* how it works */
    #how-it-works-landing {
      padding: 0 !important;
    }
  
    #how-it-works-LC {
      width: 50%;
      padding-right: 0 !important;
    }
  
    #how-it-works-RC {
      padding: 3em 1em 2em 2em;
    }
  
    #how-form {
      padding: 2em !important;
      margin-bottom: 4em;
      margin-top: 4em;
      width: 300px;
      max-width: 40vw;
    }
  
    input {
      font-size: .8em !important;
    }
  
    .theme-btn {
      font-size: .8em !important;
    }
  
    /* who we are */
    #who-we-are-L {
      padding-top: 0 !important;
      padding-left: 3em !important;
    }
  
    /* footer */
    .footer-link a {
      font-size: .8em;
    }
  
    /* about pg */
    .header-img {
      height: 200px !important;
      padding-top: 80px !important;
    }
  
    .about-content-sub {
      margin-bottom: 2em !important;
    }
  
    .about-text-L {
      padding-right: 2em !important;
    }
  
    .about-text-R {
      padding-left: 2em !important;
    }
  
    .about-content {
      padding-bottom: 6em !important;
    }
  
    .contact-img {
      height: 1.2em;
      margin-top: 0;
    }
    /* about us philosophy */
    #phil-box .col {
      padding: 1em !important;
    }
  
    #phil-box {
      margin-top: -100px !important;
    }
  
    #phil-box h3 {
      margin-top: 1em !important;
    }
  
    .value {
      padding: 1em 3em !important;
    }
  
    /* service page */
    .service-L {
      width: 40% !important;
    }
  
    .service-R {
      width: 60% !important;
      padding: 0 !important;
    }
  
    .service-page-box {
      margin: 0 auto 2em auto !important;
      width: 250px !important;
      max-width: 70%;
      min-width: 10% !important;
    }
  }
  
/* phone */
@media (max-width: 767px) {
    .navbar-toggler-icon {
      width: 1.2em;
    }
    .nav-logo {
      width: 8em !important;
    }
  
    .navbar-toggler {
      padding: .2em 0.5em !important;
    }
  
    .nav-tab {
      font-size: .8em;
    }
  
    .nav-btn {
      width: 90% !important;
      margin-top: 1em !important;
    }
  
    #title {
      font-size: 1.2em !important;
      line-height: 1.8em;
    }
  
    h1 {
      font-size: 1.2em !important;
    }
  
    h2 {
      font-size: 1em !important;
    }
  
    h3 {
      font-size: .8em !important;
    }
  
    p {
      font-size: .6em !important;
    }
  
    .nav-active{
      border: none !important;
    }
  
    .nav-tab {
      border-bottom: none !important;
    }
  
    /* landing phone */
    #landing {
      padding: 15% 1em;
    }

    .landing-content-R {
      width: 100% !important;
      margin-left: 0 !important;
      text-align: center !important;
    }

    #welcome-txt {
      text-align: center;
    } 

    #identity-div {
      text-align: center !important;
    }
  
    #identity-txt {
      margin-bottom: .5em !important;
    }
  
    #landing {
      height: 250px !important;
    }
  
    /* services phone */
    .service-box {
      width: 30vh !important;
      margin:0em !important;
      box-shadow: none !important;
    }
  
    .service-box h3 {
      font-size: .6em !important;
      margin-bottom: 0.5em !important;
    }
  
    #service-landing {
      height: 140px !important;
    }
  
    .landing-boxes {
      height: 50px !important;
    }
  
    .landing-boxes-small {
      height: 30px !important;
    }
  
    /* how it works phone */
    #how-it-works-landing {
      display: block !important;
    }
  
    #how-it-works-LC {
      width: 100% !important;
      padding: 0 !important;
    }
  
    #how-form {
      width: 100% !important;
      margin-bottom: 2em;
      padding: 2em 1em !important;
    }
    
    #how-form button{
      margin-top: 2em;
    }
    
    .form-col, .form-group {
      width: 100% !important;
      margin: 0 !important;
    }

    #how-it-works-RC {
      width: 100vw !important;
      padding: 2em 4em 2em 1em;
    }
  
    label {
      font-size: .7em;
    }
  
    input {
      font-size: .7em !important;
    }
  
    /* who we are phone */
    #who-we-are-L {
      padding: 2em !important;
    }
  
    #who-we-are p {
      padding: 0 0 2em 2em !important;
      width: 100% !important;
    }
  
    /* footer phone */
    #footer {
      padding: 1em 2em;
    }

    .footer-logo {
      width: 200px !important;
      margin-top: 2em;
      margin-bottom: 1em !important;
    }
  
    .footer-link a {
      margin: 0 !important;
      font-size: .7em;
    }
  
    .contacts {
      margin-top: .5em;
      margin-left: 1em;
      width: 100% !important;
    }

    .contact-img {
      height: 1.1em;
      margin-top: 0;
    }
  
    #who-we-are-R {
      padding: 1em 2em;
    }

    #who-we-are-R img{
      width: 100%;
    }

    .theme-btn {
      font-size: .7em !important;
    }
  
    /* about pg phone */
    .header-img {
      height: 100px !important;
      padding-top: 30px !important;
    }
  
    .about-content-sub {
      margin-bottom: 2em !important;
      display: block;
    }
  
    .about-img {
      width: 100% !important;
    }
  
    .about-text-L {
      width: 100% !important;
      padding-right: 1em !important;
    }
  
    .about-text-R {
      width: 100% !important;
      padding-right: 1em !important;
      margin-top: 2em;
      padding-left: 0 !important;
    }
  
    .about-content {
      padding-bottom: 1em !important;
    }
  
    /* about us philosophy */
    #phil-box .col {
      padding: 1em !important;
    }
  
    #phil-box {
      margin: 0 !important;
      padding: 1em !important;
    }
  
    #phil-box h3 {
      margin-top: 1em !important;
    }
  
    #phil-section {
      padding: 0 !important;
    }
    
    .value {
      margin: 1em .2em !important;
      width: 10em !important;
      padding: .5em 2em !important;
      text-align: center !important;
    }
  
    /* service page phone */
    .service-L {
      width: 100% !important;
      padding: 1em;
      display: flex;
      flex-wrap: wrap;
    }
  
    .service-R {
      width: 100% !important;
      padding: 0 1em !important;
    }
  
    .service-page-box {
      margin: 0 auto 2em auto !important;
      width: 250px !important;
      min-width: 200px !important;
    }
  
    .service-R .row {
      margin-top: 0 !important;
    }

    .dropdown-item {
      font-size: .7em !important;
    }

  }
